import * as React from 'react';
import { Routes, Route } from 'react-router-dom';

import './style.scss';

// const CartCreateView = React.lazy(() => import('./views.Create'));
const OrderUpdateView = React.lazy(() => import('./views.Update'));
const OrderListView = React.lazy(() => import('./views.List'));

export default function OrderRoutes() {
  return (
    <Routes>
      {/* <Route path="cart/:cartSlug/" element={<CartUpdateView />} /> */}
      <Route path=":orderSlug/" element={<OrderUpdateView />} />
      <Route index element={<OrderListView />} />
    </Routes>
  );
}
